@import "./utils/mixins";
@import "./utils/variables";

* {
  box-sizing: border-box;
}

.page__content {
  display: flex;
  justify-content: center;
  text-align: center;
  margin: 0;
  font-family: Arial, sans-serif;
  font-size: $font-size;
}

.page__calendar {
  width: $day-size * $col-count + ($col-count - 1) + $calendar-padding * 2;
}

@import "./blocks/calendar"
